import type { Metadata } from 'next'
import './globals.css'

export const metadata: Metadata = {
  title: 'Ecosystem Biodiversity Analyzer',
  description: 'AI-powered acoustic analysis for ecosystem biodiversity assessment',
}

export default function RootLayout({
  children,
}: {
  children: React.ReactNode
}) {
  return (
    <html lang="en">
      <body className="antialiased">
        {children}
      </body>
    </html>
  )
}